// import tabBar from '../../components/zwy-tabBar/tabBar.vue';
import backTop from '@/components/back-top/back-top.vue';
export default {
	components: {
		// tabBar
		backTop
	},
	data() {
		return {
			backTop: {
				src: '../../static/back-top/top.png',
				scrollTop: 0
			},
			scrollTop: 0,
			
			currentPage:'mycenter',
			addresslist:[
				{
					name:'zdh',
					mobile:'15077144027',
					provinceName:'广西壮族自治区',
					cityName:'南宁市',
					areaName:'西乡塘区',
					village:'衡阳街道',
					address:'天海苑'
				},
				{
					name:'哈哈哈',
					mobile:'15077144027',
					provinceName:'广西壮族自治区',
					cityName:'南宁市',
					areaName:'西乡塘区',
					village:'衡阳街道',
					address:'天海苑'
				}
			]
		}
	},
	onShow:function(){
		this.getAddressList();
	},
	methods: {
		//获取我的地址列表
		getAddressList(){
			this.$api.addresslist().then(res =>
				{
					 console.log(JSON.stringify(res));
			
					if(res.list!=null){
						 this.addresslist = res.list;
					}
				}); 
		},
		setDefault(index,e){
			console.log("e==="+JSON.stringify(e.detail.value));
			console.log("index==="+JSON.stringify(index));
			if(e.detail.value==true){
				this.$api.setDefaut({id:this.addresslist[index].id}).then(res =>
				{
					 console.log(JSON.stringify(res));
					if(res.code=='0000'){
						this.addresslist.forEach(function(p){
							console.log("p===="+JSON.stringify(p));
							p.isDefault=false;
						});
						this.addresslist[index].isDefault = true;
					}
				}); 
			}
			
		},
		deleteAddress(index,id){
			this.$api.deleteAddress({id:id}).then(res =>
			{
				 console.log(JSON.stringify(res));
				if(res.code=='0000'){
					this.addresslist.splice(index,1);
					uni.showToast({
						title:'删除成功'
					})
				}
			}); 
		},
		editeAddress(id){
			uni.navigateTo({
			    url: '/pages/add-address/add-address?id='+id+'&formpage=myaddresslist'
			});
		},
		toAddAddress(){
			uni.navigateTo({
			    url: '/pages/add-address/add-address?formpage=myaddresslist'
			});
		},
		firstPage() {
			uni.redirectTo({
			    url: '/pages/index/index'
			});
		},
		shoplist() {
			uni.redirectTo({
			    url: '/pages/shoplist/shoplist'
			});
			
		},
		toCart() {
			uni.redirectTo({
			    url: '/pages/carts/carts'
			});
		},
		toMyCenter() {
			uni.redirectTo({
			    url: '/pages/mycenter/mycenter'
			});
		},
		
	},
	onPageScroll(e) {
		this.backTop.scrollTop = e.scrollTop;
	},
}
